.nav{
    display: flex;
    align-items: center;
    height:10vh;
    width: 100vw;
    justify-content: space-evenly;
}

li{
    list-style: none;
}

.nav-item{
    display: inline;
}

.nav-item-active {
    border: 2px solid blueviolet;
    border-radius: 50%;
    height: 40px;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;

}

a{
    all: unset;  
}